import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path:'/signup',
    name:'signup',
    component: () => import(/* webpackChunkName: "signup" */ '../components/Signup.vue')
  },
  {
    path:'/login',
    name:'login',
    component: () => import(/* webpackChunkName: "login" */ '../components/Login.vue')
  },
  {
    path:'/book',
    name:'book',
    component: () => import(/* webpackChunkName: "book */ '../components/Book.vue')
  },
  {
    path:'/product',
    name:'product',
    component: () => import(/* webpackChunkName: "product */ '../components/Tours.vue')
  },
  {
    path:'/destination',
    name:'destination',
    component: () => import(/* webpackChunkName: "destination */ '../components/Destination.vue')
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
